import React, { Component } from 'react'
import EditableList from '@/components/EditableList'
import UserSearchBox from '@/components/SearchBox/UserSearchBox'
import NerdIcon from '@/icons/nerd.svg'
import RemoveIcon from '@/icons/remove.svg'
import compareBy from '@/utilities/compareBy'
import './Participants.scss'

const ORDER = {
  owner: 1,
  editor: 2,
  player: 3,
  removed: 4,
}

const addParticipants = (map, users, addition) => {
  users.forEach(user => {
    map.set(user._id, {
      ...(map.get(user._id) || user),
      ...user,
      ...addition,
    })
  })
}

export default class Participants extends Component {
  static defaultProps = {
    className: '',
    editors: [],
    owners: [],
    players: [],
  }

  static getDerivedStateFromProps(props, state) {
    const { editors, owners, players } = props
    const { edits } = state

    const map = new Map()
    addParticipants(map, players, { level: 'player', order: ORDER.player })
    addParticipants(map, editors, { level: 'editor', order: ORDER.editor })
    addParticipants(map, owners, { level: 'owner', order: ORDER.owner })
    addParticipants(map, edits)

    const participants = Array.from(map.values()).sort(compareBy('order'))
    return { ...state, participants }
  }

  state = {
    edits: [],
    participants: [],
  }

  handleAddUser = user => {
    const { participants } = this.state
    if (!participants.find(p => p._id === user._id)) {
      const edits = [
        ...this.state.edits,
        { ...user, edit: 'added', level: 'player', order: ORDER.player },
      ].sort(compareBy('order'))

      this.setState({ edits })
    }
  }
  handleSetPermission = (user, level) => {
    const { participants } = this.state
    let edits = [...this.state.edits]

    if (edits.find(e => e._id === user._id)) {
      edits = edits.filter(e => e._id !== user._id)
    } else if (participants.find(p => p._id === user._id)) {
      edits.push({ ...user, edit: 'updated', level, order: ORDER[level] })
      edits.sort(compareBy('order'))
    }

    this.setState({ edits })
  }
  handleToggleRemoved = user => {
    const { participants } = this.state
    let edits = [...this.state.edits]

    if (edits.find(e => e._id === user._id)) {
      edits = edits.filter(e => e._id !== user._id)
    } else if (participants.find(p => p._id === user._id)) {
      edits.push({ ...user, edit: 'removed', level: 'removed', order: 4 })
      edits.sort(compareBy('order'))
    }

    this.setState({ edits })
  }

  render = () => {
    const { className } = this.props
    const { participants } = this.state

    return (
      <div className={`participants ${className}`}>
        <ul className="participant-list">
          {participants.map(user => (
            <li className={`participant row ${user.edit || ''}`} key={user._id}>
              <NerdIcon className="nerd icon" />
              <div className="display">
                <span className="name">{user.name}</span>
                <span className="username">{user.username}</span>
              </div>
              {user.edit !== 'removed' && (
                <EditableList
                  className="role"
                  defaultValue={user.level}
                  onChange={level => this.handleSetPermission(user, level)}
                  options={['player', 'editor', 'owner']}
                />
              )}
              <RemoveIcon
                className="remove icon"
                onClick={() => this.handleToggleRemoved(user)}
              />
            </li>
          ))}
        </ul>
        <UserSearchBox
          className="add-user"
          onSelect={this.handleAddUser}
        />
      </div>
    )
  }
}
